'use client';

import { useCallback } from 'react';

import { YaMusicContextData } from '@yandex-music-int/player-contexts';
import { MusicEntityData } from '@yandex-music-int/player-entities';
import { AudioPlayerStatus, PlayContextParams } from '@yandex-music-int/sonata';

import { useStore } from '../store/useStore.js';

import { useSonata } from './useSonata.js';

interface PlayableContext {
    isPlaying: boolean;
    isCurrent: boolean;
    togglePlay: () => void;
    restartPlay: () => void;
}

interface UsePlayableContextArgs {
    playContextParams: PlayContextParams<YaMusicContextData, MusicEntityData>;
    entityId?: string;
    //   ,   entityId,          
    shouldCheckSelfIndex?: boolean;
    //       ,       ?
    hasDuplicatesBefore?: boolean;
}

export const usePlayableContext = ({
    playContextParams,
    entityId,
    shouldCheckSelfIndex = false,
    hasDuplicatesBefore = false
}: UsePlayableContextArgs): PlayableContext => {
    const sonata = useSonata();
    const { sonataState } = useStore();

    /**
     *       
     */
    let isCurrent = false;
    /**
     *      
     */
    let isPlaying = false;

    const { contextData } = playContextParams;
    const {
        type: contextType,
        meta: { id: contextId }
    } = contextData;

    const isCurrentContext = sonataState.contextId === String(contextId) && contextType === sonataState.contextType;

    if (entityId) {
        isCurrent = sonataState.entityMeta && entityId === sonataState.entityMeta.id;
    } else {
        isCurrent = isCurrentContext;
    }

    if (shouldCheckSelfIndex && isCurrent && isCurrentContext) {
        const currentlyPlayingTrackIndex = sonata?.state.queueState.order.value[sonata?.state.queueState?.index.value];
        const trackIndex = playContextParams.queueParams?.index;

        isCurrent =
            typeof currentlyPlayingTrackIndex === 'number' &&
            typeof trackIndex === 'number' &&
            currentlyPlayingTrackIndex === trackIndex;
    }

    /*
               ,     
             
    */
    if (!isCurrentContext && hasDuplicatesBefore) {
        isCurrent = false;
    }

    isPlaying = isCurrent && sonataState.status === AudioPlayerStatus.PLAYING;

    const togglePlay = useCallback((): void => {
        const currentEntityId = sonata?.state.queueState.currentEntity.value?.entity.data.meta.id;

        /*
                   (,   ), 
                   (,   ), 
               ,     
        */
        if (typeof entityId !== 'undefined' && entityId === currentEntityId) {
            sonata?.togglePause();
            return;
        }

        sonata?.playContext(playContextParams);
    }, [playContextParams, sonata, entityId]);

    const restartPlay = useCallback((): void => {
        sonata?.setProgress(0);
    }, [sonata]);

    return { isPlaying, isCurrent, togglePlay, restartPlay };
};
